/**
 * messageGroup.js
 *  Vue 2.7 runtime-only 
 */

import Vue from 'vue';
import MessageGroup from './messageGroup.vue'; //  Vue 
import { camelcaseToHyphen } from '../../utils/util';

MessageGroup.newInstance = function (props = {}) {
  const div = document.createElement('div');
  document.body.appendChild(div);

  //  render  template runtime-only
  const messageVm = new Vue({
    data() {
      return { ...props };
    },
    render(h) {
      return h(MessageGroup, { props });
    }
  }).$mount(div);

  const instance = messageVm.$children[0]; // 

  return {
    add(options) {
      if (instance) instance.add(options);
    },
    remove(options) {
      if (instance) instance.remove(options);
    },
    component: instance,
    destroy() {
      if (instance) instance.closeAll();
      setTimeout(() => {
        if (div.parentNode) div.parentNode.removeChild(div);
      }, 500);
    }
  };
};

export default MessageGroup;